import React, {Component} from 'react';
import SlideShow from './SlideShow.js'
import {Fragment} from 'react';


import makerfaire from './media/makerfaire.jpg'
import tecexpo_poster from './media/tecexpo_poster.png'
import tecexpo_folding from './media/tecexpo_folding.gif'
import swe_postcard from './media/swe_postcard.PNG'
import connect_report from './media/connect_report.png'

let printImages = [
                    {
                      img:swe_postcard,
                      alt:"Sky, Water, Earth Postcard",
                      style:{width:"22vmax",height:"auto",border:"1px solid black",minWidth:"290px"},
                      title:"Sky, Water, Earth: Post Card",
                      description:<Fragment>In 2017 UBC ETS launched <a href='http://skywaterearth.ca' target='_blank' rel='noopener noreferrer'>Sky, Water, Earth</a>, a joint venture with the H.R. Macmillan Space Centre and Vancouver Aquarium. I worked on both print and digital assets for the project. The top image is the front of the postcard I created, and the bottom image is the back of the postcard.</Fragment>
                    },
                    {
                      img:makerfaire,
                      alt:"UBC 100 Maker Faire Poster",
                      style:{width:"22vmax",height:"auto",minWidth:"280px"},
                      title:"UBC 100 Maker Faire Poster",
                      description:<Fragment>For UBC's centennial celebration, I took on the task of organizing <a href='http://ets.educ.ubc.ca/ubc-centennial-maker-faire/' target='blank' rel='noopener noreferrer'>UBC's first Mini Maker Faire</a>! There were approximately 200 attendees of varying age ranges working at nine different stations. I was responsible for overall organization, fundraising, purchasing &amp; budget, communication, design and marketing. </Fragment>
                    },
                    {
                      img:connect_report,
                      alt:"Departmental Usage Report Infographic",
                      style:{width:"62%",height:"auto",minWidth:"290px"},
                      title:"Departmental Usage Report",
                      description:<Fragment>This infographic was designed for UBC's Language and Literacy Education department in the Faculty of Education. It was circulated at a staff meeting as a one-pager to show how engaged faculty were with the Learning Management System at the time.</Fragment>
                    },
                    {
                      img:tecexpo_poster,
                      alt:"TEC Expo Poster",
                      style:{width:"80%",height:"auto",border:"1px solid black"},
                      title:"TEC Expo Posters (2013-2018)",
                      description:<Fragment>This poster is the keystone design for the <a href='http://ets.educ.ubc.ca/tec-expo-2018/' target='_blank' rel="noopener noreferrer">TEC Expo</a> event held by Educational Technology Support in the Faculty of Education at the University of British Columbia. This is the first poster that was conceptualized and then used to inform the design of other TEC Expo pieces. I also hosted the event in 2016 and interviewed all of the presenters while livestreaming on Facebook (to watch visit the <a href='https://www.facebook.com/pg/UBCpdce/videos/' target='_blank' rel="noopener noreferrer">PDCE Facebook Page</a>).</Fragment>
                    },
                    {
                      img:tecexpo_folding,
                      alt:"TEC Expo Folding Brochure",
                      style:{width:"80%",height:"auto",border:"1px solid black"},
                      title:"TEC Expo Folding Brochure",
                      description:<Fragment>An artifact from TEC Expo that I am especially proud of is the folding brochure. The brochure is printed on 11x17 paper and folded into a packet (folding process shown above). Each step of opening the folded packet reveals history and information about the event, eventually opening to reveal the presenters and event map for both days. </Fragment>
                    },
                  ]
class PrintExamples extends Component{
  render(){
    return(
      <SlideShow imageList={printImages} setWindowTitle={this.props.setWindowTitle} winRef="_print" />
    )
  }
}

export {PrintExamples,
        printImages};



