import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { BrowserView, MobileView, isBrowser, isMobile } from "react-device-detect";
import Billstagram from './Billstagram.js'


import './css/App.css';


//desktop components
import MacWindow from './MacWindow.js';
import MacFolder from './MacFolder.js';
import MacBackground from './media/macbackground.jpg'

//Desktop Folders//
import Welcome from './Welcome.js'
import {PrintExamples} from './PrintExamples.js'
import {VideoExamples} from './VideoExamples.js'
import {DigitalExamples} from './DigitalExamples.js'


class App extends Component {
  constructor(){
    super();

    this.openWindow = this.openWindow.bind(this);
    this.placeOnTop = this.placeOnTop.bind(this);
    this.resetWindowZ = this.resetWindowZ.bind(this);
    this.clearFolderHighlight = this.clearFolderHighlight.bind(this);
    this.setWindowTitle = this.setWindowTitle.bind(this);
    this.dockPeek = this.dockPeek.bind(this);
    this.state = {
                  folderList:[
                              {id:1,ref:"_welcome",title:"Welcome!", width:"80vmax", height:800, folderX:50, folderY:25, windowX:550, windowY:25, content:<Welcome/>,isHighlighted:1},
                              {id:2,ref:"_print",title:"Print Design Examples", width:"35vmax", height:"45vmax", folderX:50, folderY:250, windowX:"18vmax", windowY:15, mobileX:0, mobileY:0, minW:"378px", minH:"600px", content:<PrintExamples setWindowTitle={this.setWindowTitle}/>},
                              {id:3,ref:"_video",title:"Video/Animation Examples", width:"45vmax", height:"31vmax", folderX:50, folderY:125, windowX:"19vmax", windowY:75, mobileX:0, mobileY:0, minW:"450px", minH:"355px", content:<VideoExamples setWindowTitle={this.setWindowTitle}/>},
                              {id:4,ref:"_digital",title:"Digital Design Examples", width:"50vmax", height:"37vmax", folderX:50, folderY:375, windowX:"21vmax", windowY:100, mobileX:0, mobileY:0, minW:"700px", minH:"525px", content:<DigitalExamples setWindowTitle={this.setWindowTitle}/>}
                            ],
                windowList:[{id:1,ref:"_welcome",title:"Welcome!", width:"41.5vmax", height:"30vmax", folderX:50, folderY:25, windowX:"30vmax", windowY:50, minH:"625px", minW:"550px", content:<Welcome/>, bg:"#484a47"}],
                  lastDragged:null
                  };
  }
  placeOnTop(e){
    var currentObj = e.target.closest(".window")||e.target.closest(".MacFolder");
    if(!currentObj){
      return;
    };
    let tempState = Object.assign({},this.state)
    tempState["lastDragged"] = currentObj;
    this.setState(tempState)
    currentObj.style.zIndex = 999;
  }

  clearFolderHighlight(e){
    let allFolders = document.querySelectorAll(".MacFolder");
        for(let i=0;i<allFolders.length;i++){
          allFolders[i].classList.remove("highlighted")
        }
  }
  dockPeek(e){
    let theDock = ReactDOM.findDOMNode(this.refs["_dock"]);
    if(theDock.style.bottom==="-1vmax"){
      theDock.style.bottom="2vmax"
    }else{
      theDock.style.bottom="-1vmax"
    }
  }
  resetWindowZ(e){
    let lastDragged = this.state.lastDragged;
    if(lastDragged===null){return}
    //if the last thing dragged was a window, make sure the window dragged stays on top of the other windows
    if(String(lastDragged.classList).indexOf("window")!==-1){
      let elems = document.querySelectorAll(".window");
      [].forEach.call(elems, function(el) {
          el.style.zIndex = 1;
      });
      lastDragged.style.zIndex = 2;
    }

    if(String(lastDragged.classList).indexOf("MacFolder")!==-1){
      console.log("folder was dragged")
      let elems = document.querySelectorAll(".MacFolder");
      [].forEach.call(elems, function(el) {
          el.style.zIndex = 0;
      });
    }
  }
  openWindow(windowObj){
    //if the window 'id' already exists, show/hide the window and exit before creating a new window

    if(windowObj.ref in this.refs){
      let currentWindow = ReactDOM.findDOMNode(this.refs[windowObj.ref])
      let elems = document.querySelectorAll(".window");
      [].forEach.call(elems, function(el) {
          el.style.zIndex = 1;
      });
      currentWindow.style.zIndex=2;
      if(this.refs[windowObj.ref].state.hideWindow === true){
        this.refs[windowObj.ref].setState({hideWindow:false});
      }
      return;
    }

    //if we made it here then we are creating a new window so let's append the window object to the App state (respecting immutability)
    let stateCopy = Object.assign({},this.state);
    stateCopy["windowList"].push(windowObj);
    this.setState(stateCopy)
  }

  setWindowTitle(newTitle,winRef){
    console.log(this)
    console.log('refs',this.refs);
    this.refs[winRef].setMyTitle(newTitle);
  }
  render() {

    document.addEventListener("mousedown", this.placeOnTop)
    document.addEventListener("mouseup",this.resetWindowZ)
    var docBody = document.getElementsByTagName("BODY")[0]
    docBody.addEventListener("click",this.clearFolderHighlight)
    docBody.style.background='url('+MacBackground+') no-repeat center center fixed'

    return (

          <div className="App">
    <span style={{display:'none'}}>{docBody.style.background='url('+MacBackground+') no-repeat center center fixed'}</span>


          <BrowserView device={isBrowser}>
            {this.state.folderList.map((folder)=>{
              return <MacFolder title={folder.title} key={folder.id} open={this.openWindow} window={folder} xPos={folder.folderX} yPos={folder.folderY} />
            })}

            {this.state.windowList.map((window)=>{
              return <MacWindow id={window.ref} title={window.title}  key={window.id} width={window.width} height={window.height} xPos={window.windowX} yPos={window.windowY} content={window.content} ref={window.ref} bg={window.bg} fg={window.fg} minH={window.minH} minW={window.minW}  />
            })}
            </BrowserView>
          <MobileView device={isMobile}>
            <Billstagram />
          </MobileView>
          </div>

        )}}





export default App;



