import React, {Component} from 'react';
import Draggable from 'react-draggable';
import './css/MacFolder.css'
import icon from './media/macfolder.png';

var defaultClass = "";

class MacFolder extends Component{
  constructor(props){
    super(props);
    this.openWindow = this.openWindow.bind(this);
    this.addHighlight = this.addHighlight.bind(this);
    this.removeHighlight = this.removeHighlight.bind(this);
    if(this.props.window.isHighlighted===1){
      defaultClass="MacFolder highlighted"
    }else{
      defaultClass="MacFolder"
    }
  }
  openWindow(e){
    this.addHighlight(e)
    this.props.open(this.props.window);
  }
  addHighlight(e){
    let selectedFolder = e.target.closest(".MacFolder");
    selectedFolder.classList.add("highlighted")
  }
  removeHighlight(e){
    let selectedFolder = e.target.closest(".MacFolder");
    selectedFolder.classList.remove("highlighted")
  }
  render(){
    return(
      <Draggable bounds="#root" axis="both">
      <div className={defaultClass} onClick={this.openWindow} window={this.props.window} style={{top:this.props.yPos,left:this.props.xPos}}>
        <img src={icon} alt="mac folder icon" draggable="false" />
        <p>{this.props.title}</p>
      </div>
      </Draggable>
    );
  }
}

export default MacFolder;



