import React, {Component} from 'react';
import ReactDOM from 'react-dom';
import Img from 'react-image';
import Loader from 'react-loader-spinner';
import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import faChevronRight from '@fortawesome/fontawesome-free-solid/faChevronRight'
import faChevronLeft from '@fortawesome/fontawesome-free-solid/faChevronLeft'
import './css/SlideShow.css'

var prefix="";

class SlideShow extends Component{
  constructor(props){
    super(props);
    this.state = {
      currentIndex:0,
      images:this.props.imageList,
    }
    this.nextImage = this.nextImage.bind(this);
    this.prevImage = this.prevImage.bind(this);

    if(this.props.winRef==="_print"){
      prefix = "Print Design Example ";
    }else{
      prefix = "Digital Design Example "
    }
  }

  nextImage(e){
    let holder = ReactDOM.findDOMNode(this.refs["_imgHolder"]);
    holder.style.opacity = 0;

    var tempState = Object.assign({},this.state)
    if(tempState["currentIndex"]+1 <= tempState["images"].length-1){
      tempState["currentIndex"] = tempState["currentIndex"]+1;
    }else{
      tempState["currentIndex"] = 0;
    }

    this.temp = function(){
      this.setState(tempState)
      holder.style.opacity = 1
      this.props.setWindowTitle(prefix+(this.state.currentIndex+1)+"/"+this.state.images.length,this.props.winRef);
    }


    setTimeout(this.temp.bind(this),500)
  }
  prevImage(){
    let holder = ReactDOM.findDOMNode(this.refs["_imgHolder"]);
    holder.style.opacity = 0;

    var tempState = Object.assign({},this.state)
    if(tempState["currentIndex"]-1 < 0){
      tempState["currentIndex"] = tempState["images"].length-1;
    }else{
      tempState["currentIndex"] = tempState["currentIndex"]-1;
    }

    this.temp = function(){
      this.setState(tempState)
      holder.style.opacity = 1
      this.props.setWindowTitle(prefix+(this.state.currentIndex+1)+"/"+this.state.images.length,this.props.winRef);
    }

    var prefix ="";
    if(this.props.winRef==="_print"){
      prefix = "Print Design Example ";
    }
    setTimeout(this.temp.bind(this),500)
  }
  render(){

    var imgObj = this.state.images[this.state.currentIndex];
    return(
        <div id="slideshowHolder">
        <FontAwesomeIcon id="leftBtn" icon={faChevronLeft} size="2x" style={{position:'absolute',top:"50%",left:"5%",cursor:"pointer"}} onClick = {this.prevImage} />
        <FontAwesomeIcon id="rightBtn" icon={faChevronRight} size="2x" style={{position:'absolute',top:"50%",right:"5%",cursor:"pointer"}} onClick={this.nextImage} />
      <div id="imgHolder" ref="_imgHolder">
        <h1 id="imgTitle">{imgObj.title}</h1>
      <Img src={imgObj.img} alt={imgObj.alt} style={imgObj.style} loader={<Loader type="ThreeDots" color="#cccccc" height={80} width={80} />} />
        <p id="imgDesc" style={{width:"80%",textAlign:"left",margin:"0 auto",padding:"1em"}}>{imgObj.description}</p>
      </div>
      </div>
    )
  }
}

export default SlideShow;



