import React, {Component} from 'react';
import SlideShow from './SlideShow.js'
import {Fragment} from 'react';

import swe_badges from './media/swe_badges.png';
import tmh_banners from './media/tmh_banners.png';
import coursedev from './media/coursedev.png';

let digitalImages = [
                    {
                      img:swe_badges,
                      alt:"Sky, Water, Earth: Badges",
                      style:{width:"80%",height:"auto",minHeight:"275px"},
                      title:"Sky, Water, Earth: Badges",
                      description:<Fragment>Designed for the <a href="http://www.skywaterearth.ca" target="_blank" rel="noreferrer noopener">Sky, Water, Earth</a> project, these badges are awarded to participants to show that they have completed a learning playlist. The artwork was created in Adobe Illustrator and some FontAwesome icons were used as well as some CC0 licensed vectors from <a href="http://www.pixabay.com" target="_blank" rel="noopener noreferrer">pixabay.com</a>.</Fragment>
                    },
                    {
                      img:tmh_banners,
                      alt:"Teen Mental Health: Module Banners",
                      style:{width:"58vh",height:"auto"},
                      title:"Teen Mental Health: Module Banners",
                      description:<Fragment>Module header images for <a href="http://pdce.educ.ubc.ca/mentalhealth/" target="_blank" rel="noopener noreferrer">Bringing Mental Health to Schools</a>. Each unit banner is colour coded to materials contained within in the unit to help users easily orient themselves visually to which unit they are in.</Fragment>
                    },
                    {
                      img:coursedev,
                      alt:"UBC Faculty of Education Online Courses",
                      style:{width:"58vh",height:"auto",border:"1px solid #ccc"},
                      title:"UBC Faculty of Education Online Courses (2013-2018)",
                      description:<Fragment>I created the <a href="https://virtual.educ.ubc.ca/tools/template.html" target="_blank" rel="noopener noreferrer">style guide</a> for the UBC Faculty of Education's online courses from 2013-2018. These courses were offered in <em>Blackboard Learn</em> and the new page styles also included scripts I authored that provided both mobile device compatibility and the ability for pages to be printed, two features <em>BlackBoard</em> was lacking.</Fragment>
                    },
                  ]
class DigitalExamples extends Component{
  render(){
    return(
      <SlideShow imageList={digitalImages} setWindowTitle={this.props.setWindowTitle} winRef="_digital" />
    )
  }
}

export {DigitalExamples, digitalImages};



