import React, {Component, Fragment} from 'react';
import './css/VideoExamples.css'
import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import faChevronRight from '@fortawesome/fontawesome-free-solid/faChevronRight'
import faChevronLeft from '@fortawesome/fontawesome-free-solid/faChevronLeft'
const iframeStyle = {height:"16.5vmax",width:"30vmax",minWidth:"353px",minHeight:"181px"}

let videoList = {
                currentVideo:0,
                videos:[
                {
                  embed:<Fragment><iframe title="Sky, Water, Earth Promo Spot (Video)" style={iframeStyle} src="https://www.youtube.com/embed/-7Rc9yDASn0?rel=0&amp;controls=1&amp;showinfo=0" frameBorder="0" allow="autoplay; encrypted-media" allowFullScreen></iframe></Fragment>,
                  title:"Sky, Water, Earth: Promo Animation",
                  description:<Fragment>I created this video for the <a href="http://skywaterearth.ca" target="_blank" rel="noopener noreferrer">Sky, Water, Earth</a> project using Adobe After effects for the animation and Adobe Illustrator for designing many of the assets (a few are free <a href="https://creativecommons.org/share-your-work/public-domain/cc0/" target="_blank" rel="noreferrer noopener">CC0 licensed</a> vectors, the rest are original artwork).</Fragment>
                },
                {
                  embed:<Fragment><iframe title="Clinton Charlie - Paddle Song (Video)" style={iframeStyle} src="https://www.youtube.com/embed/q_lqlnJ6L_I?rel=0&amp;showinfo=0" frameBorder="0" allow="autoplay; encrypted-media" allowFullScreen></iframe></Fragment>,
                  title:"Clinton Charlie: Paddle Song",
                  description:<Fragment>This video was shot with 2 cameras (Canon 7D and Canon Rebel T3i). Audio was captured using a Røde NTG1 Condenser Mic (in windy conditions) into a handheld Roland R-05 handheld Audio Recorder. I was responsible for main camera, sound capture &amp; editing (Adobe Audtion), video editing (Adobe Premiere) and titling (Adobe After Effects).</Fragment>
                },
                {
                  embed:<Fragment><iframe style={iframeStyle} title="Virginia Morgan Interview (Video)"  src="https://www.youtube.com/embed/8baH2Ob2f-c?rel=0&amp;showinfo=0" frameborder="0" allow="autoplay; encrypted-media" allowfullscreen></iframe></Fragment>,
                  title:"Virginia Morgan Interview",
                  description:<Fragment>This video was recorded as part of a series for <a href="https://www.edx.org/course/reconciliation-through-indigenous-education" target="_blank" rel="noopener noreferrer">Reconciliation Through Indigenous Education</a> a UBC MOOC offered on edX.org. This video was shot with 2 cameras and audio was caputred using two wireless lavalier mics direct into camera (windy conditions). I was responsible for location scouting, lighting, audio setup, and camera 1 and 2 framing and setup.</Fragment>
                },
                {
                  embed:<Fragment><iframe title="Elder Lawrence Michel Interview (Video)" style={iframeStyle} src="https://www.youtube.com/embed/nbye3B40UZw?rel=0&amp;showinfo=0" frameBorder="0" allow="autoplay; encrypted-media" allowFullScreen ></iframe></Fragment>,
                  title:"Elder Lawrence Michel Interview",
                  description:<Fragment>This video was done in studio, using a black felt backdrop. I was responsible for lighting, direction, editing, sound and titling. This interview was a challenging edit as the Elder was feeling under the weather that day and had many coughing fits during filming. There was also a lack of close-up camera for the interviewer.</Fragment>
                },
                      {
                  embed:<Fragment><iframe title="TEC Expo Hologram video" style={iframeStyle} src="https://www.youtube.com/embed/Or1gVPOD-LM?rel=0&amp;showinfo=0" frameBorder="0" allow="autoplay; encrypted-media" allowFullScreen></iframe></Fragment>,
                  title:"TEC Expo: Hologram Video",
                  description:<Fragment>I created this video to work with a <a href="http://www.instructables.com/id/3D-Holograms-Using-Phone/" target="_blank" rel="noopener noreferrer">DIY hologram projector</a> for <a href="http://ets.educ.ubc.ca/tec-expo-2016-photo-gallery/" target="_blank" rel="noopener noreferrer">TEC Expo 2016</a>. I had created a display using discarded electronics and a Raspberry Pi that was used to project the hologram during the event. It's a lot of fun being a 'hologram'! Highly recommended!</Fragment>
                }
                ]
              }



class VideoExamples extends Component{
  constructor(){
    super();
    this.state= videoList;
    this.prevVideo = this.prevVideo.bind(this);
    this.nextVideo = this.nextVideo.bind(this);
  }
  prevVideo(){
    let temp = this.state;
    if(temp.currentVideo-1 < 0){
      temp.currentVideo = temp.videos.length-1;
    }else{
      temp.currentVideo--;
    }
    this.setState(temp);
    this.props.setWindowTitle("Video/Animation example "+(this.state.currentVideo+1)+"/"+this.state.videos.length,"_video");
  }
  nextVideo(){
    let temp = this.state;
    if(temp.currentVideo+1 > temp.videos.length-1){
      temp.currentVideo = 0;
    }else{
      temp.currentVideo++;
    }
    this.setState(temp);
    this.props.setWindowTitle("Video/Animation example "+(this.state.currentVideo+1)+"/"+this.state.videos.length,"_video");
  }
  render(){
    let currentVideo = this.state.videos[this.state.currentVideo]
    return(
      <div>
      <h1 id="videoTitle">{currentVideo.title}</h1>
      <div id="arrowLeft"><FontAwesomeIcon size="2x" icon={faChevronLeft} onClick={this.prevVideo} style={{cursor:"pointer"}}/></div>
        {currentVideo.embed}
      <div id="arrowRight"><FontAwesomeIcon size="2x" icon={faChevronRight} onClick={this.nextVideo} style={{cursor:"pointer"}}/></div>
      <div id="videoDescription"><p>{currentVideo.description}</p></div>
      </div>
    )
  }
}

export {VideoExamples,videoList};



