import React, {Component} from 'react';
import './css/Billstagram.css';
import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import faEnvelope from '@fortawesome/fontawesome-free-solid/faEnvelope'
import faPhone from '@fortawesome/fontawesome-free-solid/faPhone'
import faHeart from '@fortawesome/fontawesome-free-solid/faHeart'
import faChevronRight from '@fortawesome/fontawesome-free-solid/faChevronRight'
import Img from 'react-image';
import Loader from 'react-loader-spinner';

import headshot from './media/headshot.jpg';

import {printImages} from './PrintExamples.js';
import {digitalImages} from './DigitalExamples.js'
import {videoList} from './VideoExamples.js'


class Billstagram extends Component{
  constructor(){
    super();
    this.state = {printIndex:0,
                  videoIndex:0,
                  digitalIndex:0,}

    this.printClick = this.printClick.bind(this);
    this.digitalClick = this.digitalClick.bind(this);
    this.videoClick = this.videoClick.bind(this);
    this.randomNum = this.randomNum.bind(this);
    this.closeIntro = this.closeIntro.bind(this);
  }
  randomNum(){
    return Math.floor(Math.random()*250+1)
  }
  closeIntro(){
    document.getElementById("btg_stories").classList.add("small");
  }
  printClick(){
    let temp = this.state;
    if(temp.printIndex+1 <= printImages.length-1){
      temp.printIndex++;
    }else{
      temp.printIndex=0;
    }
    this.setState(temp)
  }
  digitalClick(){
    let temp = this.state;
    if(temp.digitalIndex+1 <= digitalImages.length-1){
      temp.digitalIndex++;
    }else{
      temp.digitalIndex=0;
    }
    this.setState(temp)
  }
  videoClick(){
    let temp = this.state;
    if(temp.videoIndex+1 <= videoList.videos.length-1){
      temp.videoIndex++;
    }else{
      temp.videoIndex=0;
    }
    this.setState(temp);
  }
  render(){
    return(
<div>
      <div style={{backgroundColor:"white",overFlowX:"hidden"}}>
        <div id='btg_header'>
          <a href="mailto:bill.r.pickard@gmail.com"><FontAwesomeIcon icon={faEnvelope} id='iconMail' size="2x"/></a>
          <h1>Bill R. Pickard</h1>
          <h3>Freelance Designer/Developer</h3>
          <a href="tel:+16044440187"><FontAwesomeIcon icon={faPhone} id="iconMobile" size="2x"/></a>
        </div>
        <div id="btg_stories">
        <span style={{float:"right",marginTop:"-30px",marginRight:"-10px",fontSize:"18px",padding:"10px"}} onClick={this.closeIntro}>&#x2715;</span>
        <p style={{verticalAlign:"top"}}>Welcome! I'm a designer/developer with 15 years expereince working in higher education, building courses, departmental websites and interactive tools. This is my first build using <em>react.js</em> (sans backend database). I have really enjoyed learning <em>react.js</em> so far and I am really looking forward to creating more projects with it. With regards to my skills, I am someone than can help you with:</p>
        <ul>
        <li>building/designing/coding your website from the ground up;</li>
        <li>designing a course or training session for your business or organization;</li>
        <li>creating original images/artwork/logos for your project;</li>
        <li>producing original audio, video, or animations for your project;</li>
        <li>troubleshooting/cleaning/repairing your computer (Windows or Mac);</li>
        <li>creating a custom touchscreen kiosk for your business;</li>
        <li>and more! (just ask)</li>
        </ul>
        <p>If you are interested in working with me, please contact me for a consultation! Enjoy the site!</p>
        </div>

        <div className="sshow">
            <div style={{backgroundColor:"white"}}>
            <div style={{textAlign:"left"}}>
            <Img src={headshot} style={{width:"25px",height:"25px",borderRadius:"999px",float:"left",verticalAlign:"middle",marginTop:"-2px",marginRight:"3px",marginLeft:"10px",border:"2px solid orange",padding:"2px"}} />
            <p style={{verticalAlign:"middle",fontSize:"12px"}}><strong>Print example {this.state.printIndex+1}/{printImages.length}</strong></p>
            <p style={{fontSize:"10px",marginTop:"-12px"}}>{printImages[this.state.printIndex].alt}</p>
            <FontAwesomeIcon icon={faChevronRight} style={{float:"right",marginTop:"-42px",marginRight:"10px",paddingLeft:"20px"}} size="2x" onClick={this.printClick}/>
            </div>
              <Img src={printImages[this.state.printIndex].img} alt={printImages[this.state.printIndex].alt} loader={<Loader type="ThreeDots" color="#cccccc" height={80} width={80} />} style={{maxWidth:"100%",height:"auto",width:"auto",cursor:"pointer"}} />
            </div>
            <div style={{textAlign:"left",paddingLeft:"8px"}}>
            <FontAwesomeIcon icon={faHeart} size="2x" style={{color:"#ccc",width:"25px"}} onClick={(e)=>{e.target.classList.toggle('red')}} />
            <p style={{fontSize:"13px",marginTop:"0px"}}>{this.randomNum()} views &bull; Liked by <strong>billrpickard</strong></p>
            <p style={{fontSize:"13px",marginTop:"-10px",paddingBottom:"15px"}}><strong>billrpickard</strong> {printImages[this.state.printIndex].description}</p>
            </div>
        </div>
        <div className="sshow">
            <div style={{backgroundColor:"white"}}>
            <div style={{textAlign:"left"}}>
            <Img src={headshot} style={{width:"25px",height:"25px",borderRadius:"999px",float:"left",verticalAlign:"middle",marginTop:"-2px",marginRight:"3px",marginLeft:"10px",border:"2px solid orange",padding:"2px"}} />
            <p style={{verticalAlign:"middle",fontSize:"12px"}}><strong>Digital example {this.state.digitalIndex+1}/{digitalImages.length}</strong></p>
            <p style={{fontSize:"10px",marginTop:"-12px"}}>{digitalImages[this.state.digitalIndex].alt}</p>
            <FontAwesomeIcon icon={faChevronRight} style={{float:"right",marginTop:"-42px",marginRight:"10px",paddingLeft:"20px"}} size="2x" onClick={this.digitalClick}/>
            </div>
              <Img src={digitalImages[this.state.digitalIndex].img} alt={digitalImages[this.state.digitalIndex].alt} loader={<Loader type="ThreeDots" color="#cccccc" height={80} width={80} />}style={{maxWidth:"100%",height:"auto",width:"auto",cursor:"pointer"}} />
            </div>
            <div style={{textAlign:"left",paddingLeft:"8px"}}>
            <FontAwesomeIcon icon={faHeart} size="2x" style={{color:"#ccc",width:"25px"}} onClick={(e)=>{e.target.classList.toggle('red')}}/>
            <p style={{fontSize:"13px",marginTop:"0px"}}>{this.randomNum()} views &bull; Liked by <strong>billrpickard</strong></p>
            <p style={{fontSize:"13px",marginTop:"-10px",paddingBottom:"15px"}}><strong>billrpickard</strong> {digitalImages[this.state.digitalIndex].description}</p>
            </div>
        </div>
        <div className="sshow">
            <div style={{backgroundColor:"white"}}>
            <div style={{textAlign:"left"}}>
            <Img src={headshot} style={{width:"25px",height:"25px",borderRadius:"999px",float:"left",verticalAlign:"middle",marginTop:"-2px",marginRight:"3px",marginLeft:"10px",border:"2px solid orange",padding:"2px"}} />
            <p style={{verticalAlign:"middle",fontSize:"12px"}}><strong>Video/Animation example {this.state.videoIndex+1}/{videoList.videos.length}</strong></p>
            <p style={{fontSize:"10px",marginTop:"-12px"}}>{videoList.videos[this.state.videoIndex].title}</p>
            <FontAwesomeIcon icon={faChevronRight} style={{float:"right",marginTop:"-42px",marginRight:"10px",paddingLeft:"20px"}} size="2x" onClick={this.videoClick}/>
            </div>
            <div id="videoFrame">{videoList.videos[this.state.videoIndex].embed}</div>
            </div>
            <div style={{textAlign:"left",paddingLeft:"8px"}}>
            <FontAwesomeIcon icon={faHeart} size="2x" style={{color:"#ccc",width:"25px"}} onClick={(e)=>{e.target.classList.toggle('red')}}/>
            <p style={{fontSize:"13px",marginTop:"0px"}}>{this.randomNum()} views &bull; Liked by <strong>billrpickard</strong></p>
            <p style={{fontSize:"13px",marginTop:"-10px",paddingBottom:"15px"}}><strong>billrpickard</strong> {videoList.videos[this.state.videoIndex].description}</p>
            </div>
        </div>
        <div className="mobileFooter">
        <p>&copy; Bill R. Pickard, 2018</p> 
        </div>
      </div>
      </div>
    )
  }
}

export default Billstagram;



