import React, { Component } from 'react';
import Draggable from 'react-draggable';
import './css/MacWindow.css';

class MacWindow extends Component{
  constructor(props){
   super(props);
   this.state = {hideWindow:false,windowTitle:props.title }
     this.closeMe = this.closeMe.bind(this);
     this.setMyTitle = this.setMyTitle.bind(this);
  }
  closeMe(){
    this.setState({hideWindow:true});
    var iframes = document.getElementsByTagName('iframe');
    if(this.props.id==="_video"){
      for(let _i = 0;_i<iframes.length;_i++){
        iframes[_i].src = iframes[_i].src;
      }
    }
  }
  setMyTitle(newTitle){
    let temp = this.state
    temp.windowTitle=newTitle;
    this.setState(temp)  
  }
  render(){
    const windowStyle = this.state.hideWindow ? {display:'none'} : {width:this.props.width,height:this.props.height,left:this.props.xPos,top:this.props.yPos,zIndex:2,backgroundColor:this.props.bg,color:this.props.fg,minHeight:this.props.minH,minWidth:this.props.minW};
    return(
      <Draggable handle=".titlebar" bounds="#root" axis="both" >
      <div className="window" style={windowStyle} >
  <div className="titlebar">
    <div className="buttons">
      <div className="close" onClick={this.closeMe}>
        <a href={null} className="closebutton" style={{fontFamily:"Arial",fontSize:"10px"}}>&#x2715;</a>
      </div>
      <div className="minimize">
        <a className="minimizebutton" href={null}><span><strong>&nbsp;</strong></span></a>
      </div>
      <div className="zoom">
        <a className="zoombutton" href={null}><span><strong>&nbsp;</strong></span></a>
      </div>
    </div>
    <p>{this.state.windowTitle}</p>
  </div>
  <div className="content" >
  {this.props.content}
  </div>
</div>
      </Draggable>

    );
  }
}

export default MacWindow;



