import React, {Component} from 'react';
import {isMobile } from "react-device-detect";
import headshot from './media/headshot.jpg'
import Img from 'react-image';
import Loader from 'react-loader-spinner';

import './css/Welcome.css'

let defaultStyle = "";

class Welcome extends Component{
  constructor(props){
    super(props);
    if(isMobile===true){
      defaultStyle="mobile"
    }
  }
  render(){
    return(
      <div id="welcomeHolder" className={defaultStyle}>
      <div className={'welcome_top '+defaultStyle}>
            <Img id="bioImage" className={defaultStyle} src={headshot} loader={<Loader type="ThreeDots" color="#cccccc" height={80} width={80}/>} />
            <h1 id="bioTitle" className={defaultStyle}>Bill R. Pickard</h1>
            <h3 id="bioSubtitle" className={defaultStyle}>Freelance Designer/Developer | <a style={{textDecoration:"underline"}}href="mailto:bill.r.pickard@gmail.com">bill.r.pickard@gmail.com</a></h3>
      </div>
        <div className={'welcome_bottom '+defaultStyle}>
        <h1>Welcome to my collection of works!</h1>
        <p>Over the past 15 years I have been working as an instructional media developer, creating custom graphics, animations, interactive exercises, and instructional videos for higher education.</p>

        <p>I am an expert in most pieces of software in the Adobe Creative Suite package (Photoshop, Illustrator, etc.) and also enjoy developing in modern javascript frameworks such as react.js and jQuery.</p>

        <p>I am an excellent communicator and can bring your concept from napkin to notebook, working closely with you along the way to ensure you get the design and quality you're after.</p>

        <h1>What's next?</h1>
        <p>This is the react.js version of my portfolio. Please explore and take a look around (I suggest visiting on mobile for the <em>Billstagram</em> experience as well). If you like what you see, please contact me for a consultation. Thanks for visiting!</p>
               </div>
      </div>

    )
  }
}

export default Welcome;



